import { Heading } from '@ali/4ever-mo';
import { HeadingPluginConfig } from './types';

const {
  Heading1,
  Heading2,
  Heading3,
  Heading4,
} = Heading;

export default function placeholder(config: HeadingPluginConfig) {
  const { locale } = config;
  return [
    {
      text: locale?.heading1Placeholder,
      when: (controller, node) => {
        return Heading1.isHeading1(node) && Heading.isEmpty(node);
      },
    },
    {
      text: locale?.heading2Placeholder,
      when: (controller, node) => {
        return Heading2.isHeading2(node) && Heading.isEmpty(node);
      },
    },
    {
      text: locale?.heading3Placeholder,
      when: (controller, node) => {
        return Heading3.isHeading3(node) && Heading.isEmpty(node);
      },
    },
    {
      text: locale?.heading4Placeholder,
      when: (controller, node) => {
        return Heading4.isHeading4(node) && Heading.isEmpty(node);
      },
    },
  ];
}
